'use client'

import Link from 'next/link';
import { useTheme } from '@/app/providers/ThemeProvider';
import { useState } from 'react';

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <header className="navbar bg-slate/50 backdrop-blur-sm shadow-lg sticky top-0 z-50 border-b border-sage/10">
      <div className="container mx-auto px-4 flex justify-between items-center">
        {/* Mobile Menu and Logo */}
        <div className="flex items-center gap-4">
          {/* Mobile Dropdown */}
          <div className="lg:hidden">
            <div className="dropdown">
              <label 
                tabIndex={0} 
                className="btn btn-ghost text-sage hover:bg-sage/10"
                onClick={() => setIsOpen(!isOpen)}
              >
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                </svg>
              </label>
              {isOpen && (
                <ul 
                  tabIndex={0} 
                  className="menu menu-sm dropdown-content mt-3 z-[1] p-2 shadow-lg bg-slate/95 backdrop-blur-sm rounded-lg w-52 border border-sage/10"
                  onClick={() => setIsOpen(false)}
                >
                  <li><Link href="/blog" className="text-peach hover:text-peach/80 py-2">Blog</Link></li>
                  <li><Link href="/music" className="text-coral hover:text-coral/80 py-2">Music</Link></li>
                  <li><Link href="/videos" className="text-rose hover:text-rose/80 py-2">Videos</Link></li>
                  <li><Link href="/photos" className="text-sage hover:text-sage/80 py-2">Photos</Link></li>
                  <li><Link href="/writings" className="text-peach hover:text-peach/80 py-2">Writings</Link></li>
                </ul>
              )}
            </div>
          </div>

          {/* Logo/Brand */}
          <Link 
            href="/" 
            className="btn btn-ghost text-xl text-sage hover:text-sage/80 transition-colors"
          >
            Felix Arash
          </Link>
        </div>

        {/* Desktop Menu */}
        <nav className="hidden lg:block flex-auto">
          <ul className="flex justify-center items-center space-x-8">
            <li>
              <Link 
                href="/blog" 
                className="text-peach hover:text-peach/80 transition-colors px-4 py-2 rounded-lg hover:bg-peach/10"
              >
                Blog
              </Link>
            </li>
            <li>
              <Link 
                href="/music" 
                className="text-coral hover:text-coral/80 transition-colors px-4 py-2 rounded-lg hover:bg-coral/10"
              >
                Music
              </Link>
            </li>
            <li>
              <Link 
                href="/videos" 
                className="text-rose hover:text-rose/80 transition-colors px-4 py-2 rounded-lg hover:bg-rose/10"
              >
                Videos
              </Link>
            </li>
            <li>
              <Link 
                href="/photos" 
                className="text-sage hover:text-sage/80 transition-colors px-4 py-2 rounded-lg hover:bg-sage/10"
              >
                Photos
              </Link>
            </li>
            <li>
              <Link 
                href="/writings" 
                className="text-peach hover:text-peach/80 transition-colors px-4 py-2 rounded-lg hover:bg-peach/10"
              >
                Writings
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  );
}