'use client'

export default function AnimatedBackground() {
  return (
    <div className="fixed inset-0 -z-10 overflow-hidden">
      <div className="absolute inset-0 opacity-20 bg-gradient-custom bg-animate">
        {/* This div uses the animations defined in globals.css */}
      </div>
      <div className="absolute inset-0 backdrop-blur-3xl" />
    </div>
  );
}